import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { CourseCategoriesPageComponent } from './course-categories-page.component';

const routes: Routes = [
  { path: '', component: CourseCategoriesPageComponent }
];

@NgModule({
  imports: [
    RouterModule.forChild(routes)
  ],
  exports: [
    RouterModule
  ]
})
export class CourseCategoriesPageRoutingModule { }
